<template>
  <div class="login">
    <b-button>{{msg}}</b-button>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      msg: '로그인'
    }
  },
  mounted(){
    
      this.$http.get('/api/session')
      .then((response) => {
        console.log('res:', response.data.res)
        if(response.data.res == 0){
          this.$router.push('/')
        }
      });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



