<template>
  <div class="hello">
    <div class="loginBox">
    <b-form @submit="onSubmit" v-if="show">
      <b-form-group id="inputGroup"
                    label="관리자 로그인"
                    label-for="input1">
        <b-form-input ref='id' id="exampleInput1"
                      class="front_input"
                      type="text"
                      v-model="form.id"
                      required
                      placeholder="아이디 입력">
        </b-form-input>
        <b-form-input id="exampleInput2"
                      class="front_input"
                      type="password"
                      v-model="form.pw"
                      required
                      placeholder="비밀번호 입력">
        </b-form-input>
      </b-form-group>
      <!-- <b-button type="submit" variant="primary">{{msg}}</b-button> -->
      <b-button v-on:click="onSubmit" variant="primary">{{msg}}</b-button>
    </b-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data: () => {
    return {
      msg: '로그인 이동',
      show: true,
      form: {
        id: '',
        pw: ''
      }
    }
  },
  mounted(){
    this.$refs.id.focus();
      this.$http.get('/api/session')
      .then((response) => {
        console.log('res:', response.data.res)
        if(response.data.res == 1){
          // this.$router.push('/Login')
        }
      });
  },
  methods: {
    loginMove: function (event) {
      this.$router.push('/Login')
    },
    onSubmit: function() {
        console.log('onSubmit');
        let data = {
          id: this.form.id,
          pw: this.form.pw
        }
      this.$http.post('/api/login', data)
      .then((response) => {
        console.log('res:', response.data.res)
        if(response.data.res == 1){
          this.$router.push('/Login')
        } else {
          this.form.id = ''
          this.form.pw = ''
          alert('잘못된 정보입니다. 다시 입력해 주세요.')
          this.$refs.id.focus()
        }
      })
      .catch(function (error) {
        console.log('error')
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.loginBox{
  position: relative;
  width: 400px;
  left: 50%;
  margin-left: -200px;
  border: steelblue 3px solid;
  padding: 20px;
}
.front_input{
  margin-top: 10px;
}
</style>



